<template>
  <div>
    <van-nav-bar title="补开发票" fixed style="height: 46px">
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left"  @click="$toPage(-1)" />
    </van-nav-bar>
    <div style="height: 46px;"></div>
    
    <van-cell-group>
      <van-cell :required="true" title="发票类型" @click="selectTypeShow" :value="invoiceTypeIndex||invoiceTypeIndex==0?invoiceType[invoiceTypeIndex].name:'请选择发票类型'" />
      <van-actionsheet v-model="show_0" title="请选择发票类型">
        <van-cell-group class="text_center">
          <van-cell v-for="(item,index) in invoiceType" :key="index" :title="item.name" @click="selectedType(index)" />
        </van-cell-group>
      </van-actionsheet>
      
      <van-cell :required="true" title="补开原因" @click="selectReasonShow" :value="invoiceReasonIndex||invoiceReasonIndex==0?invoiceReasonList[invoiceReasonIndex].label:'请选择原因'" />
      <van-actionsheet v-model="show_1" title="请选择原因" >
        <van-cell-group class="text_center">
          <van-cell v-for="(item,index) in invoiceReasonList" :key="index" @click="selectedReason(index)" :title="item.label" />
        </van-cell-group>
      </van-actionsheet>
    
      <van-field label="原因描述" type="textarea" placeholder="请简单描述退货原因" v-model="apdesc" rows="4" autosize />
      
    </van-cell-group>
      
    <div class="invoiceInfo">
      <div>公司名称：{{$loadUserInfo().comp.storeName}}</div>
      <div v-show="invoiceTypeIndex == 2">注册地址：{{$loadUserInfo().comp.address}}</div>
      <div>纳税人识别号：{{ invoiceInfo?invoiceInfo.taxpayer:'' }}</div>
      <div v-show="invoiceTypeIndex == 2">开户银行：{{ invoiceInfo?invoiceInfo.bankers:'' }}</div>
      <div v-show="invoiceTypeIndex == 2">银行账号：{{ invoiceInfo?invoiceInfo.account:'' }}</div>
      <div v-show="invoiceTypeIndex == 2">手机号码：{{ invoiceInfo?invoiceInfo.tel:'' }}</div>
    </div>

    <div class="adress" v-show="invoiceTypeIndex == 2">

    </div>
    
    <van-cell v-if="invoiceTypeIndex==1||invoiceTypeIndex==2" title="邮费" :value="'￥'+postage" value-class="red" />
    <van-button type="danger" block @click="commit">提交</van-button>

    <!-- 支付方式 -->
    <van-actionsheet
      class="payment"
      v-model="payment"
      :close-on-click-overlay="overlay"
    >
      <div
        v-for="(item, index) in payments"
        :key="index"
        @click="onSelectPay(index)"
      >
        <img :src="item.url" :alt="item.name" />
        <p class="name">{{ item.name }}</p>
      </div>
      <p class="cancel" @click="onCancel">取消</p>
    </van-actionsheet>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        orderNo:null,//订单号
        invoiceType:[{
          val:1,
          name:'电子普通发票'
        },{
          val:2,
          name:'纸质普通发票'
        },{
          val:3,
          name:'增值税专用发票'
        }],//发票类型
        invoiceTypeIndex:0,//发票类型下标
        show_0:false,//发票类型显示
        invoiceReasonList:[],//补开发票原因
        invoiceReasonIndex:null,//补开发票原因下标
        show_1:false,//补开原因显示
        apdesc:'',//原因描述
        invoiceInfo:{},//发票信息
        postage:0,//邮费
        payType:1,//支付方式
        userName: '',
        contactphone: '',
        flag: true,
        payment: false,
        payments: [
          { attr: 'wxpay', name: '微信支付', url: require("../../assets/pay/wx.png") },
          { attr: 'alipay', name: '支付宝支付', url: require("../../assets/pay/zfb.png") }
        ],
        overlay: false,
        mask: true,
        afsano: 0,
        asType: 0
      }
    },
    mounted() {
      this.orderNo = this.$route.query.orderNo;
      this.getInvoiceReasonList();//获取补开发票原因
      this.getInvoiceInfo();//获取发票信息
      this.getPostage();//获取邮费
      this.getConsignee()
    },
    methods: {
      selectTypeShow() {
        this.show_0 = true;
      },
      selectedType(val){
        this.invoiceTypeIndex = val;
        this.show_0 = false;
      },
      getInvoiceReasonList(){
        JNB.query({
          serverName: 'globalServer',
          cls: 'CommonModule',
          method: 'getDicts',
          params: {},
          callback: data => {
            data = JSON.parse(data);
            if (data.code == 200) {
              data.data = JSON.parse(data.data);
              this.invoiceReasonList = data.data.mireason;

            }
          }
        })
      },
      selectReasonShow(){
        this.show_1 = true;
      },
      selectedReason(val){
        this.invoiceReasonIndex = val;
        this.show_1 = false;
      },
      getInvoiceInfo(){
        JNB.query({
          serverName: 'userServer',
          cls: 'MyInvoiceModule',
          method: 'getInvoice',
          params: {},
          callback: data => {
            data = JSON.parse(data);

            if (data.code == 200 && data.data.length > 0) {
              this.invoiceInfo = data.data[0];
            }
          }
        })
      },
      getPostage(){
        JNB.query({
          serverName: 'orderServer'+this.$ser_in(this.$loadUserInfo().comp.storeId),
          cls: 'PayModule',
          method: 'showFeePayInfo',
          params: {
            orderno: this.orderNo,
            compid: this.$loadUserInfo().comp.storeId
          },
          callback: data => {
            data = JSON.parse(data);

            if (data.code == 200 ) {
              this.postage=data.data.payamt;
              this.mask = false
            }else{
              this.$toast(data.message)
            }
          }
        })
      },
      //  查询收货人
      getConsignee() {
        JNB.query({
          serverName: "userServer",
          cls: "MyDrugStoreInfoModule",
          method: "queryMyConsignee",
          params: {
            compid: this.$loadUserInfo().comp.storeId
          },
          callback: data => {
            let result = JSON.parse(data);
            if (result.code == 200) {
              this.user = result.data;
              result.data.forEach(arr => {
                if (arr.cstatus === 2) {
                  this.userName = arr.contactname;
                  this.contactphone = arr.contactphone;
                }
              });

              if (this.user.length === 1) {
                this.userName = this.user[0].contactname;
                this.contactphone = this.user[0].contactphone;
              }
            } else {
              console.error(data);
              this.$toast.fail(result.message);
            }
          }
        });
      },
      onSelectPay(item) {
        JNB._requestNative(this.payments[item].attr, JSON.stringify({orderno: this.orderNo,afsano: this.afsano}), data => {

          if (data == 0) {

            if(this.$store.state.payResult){
              Dialog.alert({
                title: '温馨提示',
                message: '支付成功！'
              }).then(() => {
                this.$router.push({
                  path: '/serviceAfterSalesRecord'
                })
              });
            }
          }
        })
      },
      commit() {
        if (this.invoiceType[this.invoiceTypeIndex].val == 1) {
          this.asType = 3;
        } else {
          this.asType = 4;
        }
        this.invoice = {
          invoiceInfo: {
            compName:this.$loadUserInfo().comp.storeName,
            address:this.$loadUserInfo().comp.address,
            bankers:this.invoiceInfo.bankers,
            account:this.invoiceInfo.account,
            taxpayer:this.invoiceInfo.taxpayer,
            tel:this.invoiceInfo.tel
          },
          address: {
            consignee:this.userName,
            contact:this.contactphone,
            compName:this.$loadUserInfo().comp.storeName,
            address:this.$loadUserInfo().comp.address
          }
        };
        let arr = [
          {
            gstatus: 0,
            orderno: this.orderNo,
            compid: this.$loadUserInfo().comp.storeId,
            reason: this.invoiceReasonList[this.invoiceReasonIndex].value,
            invoice: JSON.stringify(this.invoice),
            apdesc: this.apdesc,
            astype: this.asType,
            invoicetype: this.invoiceType[this.invoiceTypeIndex].val
          }
        ];

        JNB.query({
          serverName: 'orderServer'+this.$ser_in(this.$loadUserInfo().comp.storeId),
          cls: 'OrderOptModule',
          method: 'afterSaleApp',
          params: {
            orderno: this.orderNo,
            asAppArr: arr,
            asType: this.asType,
            invoType: this.invoiceType[this.invoiceTypeIndex].val
          },
          callback: data => {
            data = JSON.parse(data);

            if (data.code == 200 ) {
              this.afsano = data.data
              if(this.invoiceTypeIndex == 0){
                this.$toast(data.message)
                this.$router.push({
                  path: '/serviceAfterSalesRecord'
                })
              }else{
                this.payment = true
              }
            }else{
              this.$toast(data.message)
              this.flag = false
            }
          }
        })
      },
      payResult() {
        JNB.query({
          serverName: 'orderServer'+this.$ser_in(this.$loadUserInfo().comp.storeId),
          cls: 'PayModule',
          method: 'getFeePayResult',
          params: {
            orderno: "190702000020",
            compid: this.$loadUserInfo().comp.storeId
          },
          callback: data => {
            let result = JSON.parse(data);

            if (result.code == 200 ) {
            }else{
              this.$toast(result.message)
            }
          }
        })
      },
      onCancel() {
        this.payment = false
      }
    }
  }
</script>

<style scoped>
  p{
    margin: 0;
  }
  .invoiceInfo{
    padding: 20px 40px;
    margin: 20px 0;
    background-color: #FFFFFF;
    font-size: 28px;
    color: #666666;
    line-height: 60px;
  }
  .red{
    color: #FF4444;
  }
  .van-button{
    width: 710px;
    position: absolute;
    bottom: 30px;
    left: 20px;
    border-radius: 10px;
  }
 .payment div {
  border-bottom: 1px solid #f5f5f5;
  overflow: hidden;
  }
 .payment div .name{
    float: left;
    font-size: 28px;
    line-height: 130px;
    margin-left: 26px;
  }
 .payment div img {
    float: left;
    width: 60px;
    height: 60px;
    padding: 35px 0;
    margin-left: 250px;
  }
 .payment .cancel{
    padding: 30px;
    text-align: center;
    margin-top: 20px;
    font-size: 32px;
    border-top: 1px solid #f5f5f5;
  }
</style>
